import React , { useState , useEffect } from 'react';
import './App.css';
import ForntPageImages from './components/frontImages';
import { Router, Route} from 'react-router-dom'
import LoginForm from './components/Login';
import loginService from './service/login';
import history from './history';
import WebShop from './components/WebShop'
import {ProtectRoute } from './protetctRoute'
import auth from './auth';
import NavBaa from './components/nav';
import Footer from './components/footer'

const  App =  props  =>  {
  const [ counter, setCounter ] = useState(new Date().toDateString())
  const [password , setPassword] = useState("")
  const [username, setUsername] = useState("")
  const [user, setUser] = useState([])

  const [warning, setWarning] = useState(null)



 
  useEffect( ()  => {
    
     (async () => {
      
      
      const loggUser = window.localStorage.getItem('loginForInterface')
      //console.log(loggUser)
      if(loggUser) {
        console.log("On täällä")
        const parsedUser = JSON.parse(loggUser);
        setUser(parsedUser)
       
        auth.login()
        history.push("/pekkauserinterface")
        
      }
      
     })();
   
  }, [])
 
  

  const loginForm = () => (
    <div> 
      <LoginForm
        username={username}
        password={password}
        handleChange={handleLogin}
        handleSubmit={login}
      />
    </div>
  )
  
  const Ownuser = props => {
    if (auth.login){
      
     
    return (
    <div>
     <NavBaa time={counter.toString()} name={user.username.toString()}/>
     <br></br>
     <ForntPageImages aut={user.token}/>
     <br></br>
     <Footer />
     
   
    </div>
    )
    } else {
      return (
        <div>
         <h3>What</h3>
        </div>
      )
    }
  }
  const login = async (event) => {
     event.preventDefault()
    
     const user = {
       username: username,
       password: password
     }
    
      try {
         const res = await loginService(user);
         console.log(res.status)
         window.localStorage.setItem('loginForInterface', JSON.stringify(res.data))
         setPassword("")
         setUsername("")
         setUser(res.data)
         auth.login()
         if (auth.isAuth()) {
           history.push('/pekkauserinterface')
         } else {
           history.push('/login')
         }
    
      } catch (e) {
        console.log(e)
        setWarning("Not Correct Password or Username")
      }
     
      setTimeout(() => {
          setWarning(null)
          setUsername("")
          setPassword("")
      }, 4000)

      

   
  }
  const handleLogin = (event) => {
    
    if (event.target.name === 'password'){
      setPassword(event.target.value)
    } else if (event.target.name === 'username' ) {
      setUsername(event.target.value)
    }
  }

  const Warn = () => {
    //<Route path="*" render = {() => "404 not found"} />
    if(warning !== null){
      return (
        <div className="alert alert-danger">
           <strong>{warning}</strong>
          </div>

      )
    } else {
      return (<div></div>)
    }
  }
  
  return (
    <div className="container">
    
    <Router history={history}>
    
    

    
  
    <Warn />

      <ProtectRoute exact path="/pekkauserinterface" component={Ownuser} />

      <Route exact path="/pekkawebshop" render ={() => <WebShop />} />
     
      <Route exact path="/login" render={loginForm}  />  

     
     
     </Router>
   
   
    </div>
  );
}

export default App;

