import React from 'react';
import axios from 'axios';

const WebShopItems = (props) => {

    const s = {
        widht: "100%",
        
    }
    const removeImage = async (event) => {
        event.preventDefault()
        const config = { headers: { 'Authorization': props.aut }};
        console.log(config)
    
        const id = event.target.value;
      
        try {
           const response = await axios.delete(`http://ec2-52-48-69-81.eu-west-1.compute.amazonaws.com/api/files/${id}`, config);
           console.log("kuvan poisto ", response)
    
           
           
    
        } catch (e) {
         
        }
    }
    return (
        
        <div className="col-sm-4" key={props.kuvat._id}>  
        <div className="card" style={s}>
        
          
        <img className="card-image-top" src= {`http://ec2-52-48-69-81.eu-west-1.compute.amazonaws.com/api/files/${props.kuvat.filename}`} width="100%" height="200" alt="" />
        
       
        <div className="card-body">
        <h4 className="card-title">{(props.kuvat.metadata.artName).toUpperCase()}</h4>
        <p className="card-text">{("Valmistus vuosi:  " + props.kuvat.metadata.vuosi)}</p>
        <p className="card-text">{("Koko:  " + props.kuvat.metadata.mitat)}</p>
        
        <p className="card-text">{(props.kuvat.metadata.kuvaus)}</p>
        <hr></hr>
        <p>{(props.kuvat.metadata.hinta)} €</p> 
        
        <button value={props.kuvat._id} className="btn btn-dark btn-block mt-4">Change</button>
        <button onClick={removeImage} value={props.kuvat._id} className="btn btn-danger btn-block mt-4">Delete</button>
        
         
        </div>
        </div>
        </div>
       
       
       
       
       

    )
}

export default WebShopItems;