import React from 'react';
import { Route, Redirect } from 'react-router-dom'
import auth from './auth';


export const ProtectRoute = ({ component: Component, ...rest}) => {
    return (
        <Route
          {...rest}
          render = {props => {
              if (auth.isAuth()) {
                  console.log("täälä löyty")
                  return <Component {...props} />;
              } else {
                console.log("kävu")
               
                  return ( 
                      
                      <Redirect 
                      to = {{
                      
                          pathname: "/login",
                          state: {
                             from: props.location
                          }
                      
                        }}
                  />
                  )
                  
              }
          }}
          />
    )
}